import React from 'react';

const WeatherInfo = props => (
	<div className="infoWeath">
		{ props.city &&
		<div>
			<p>Location: <span className="weathInfoValue">{ props.city }, { props.country }</span></p>
			<p>Temperature: <span className="weathInfoValue">{ props.temp }&deg;C</span></p>
			<p>Pressure: <span className="weathInfoValue">{ props.pressure }</span></p>
			<p>Sunset: <span className="weathInfoValue">{ props.sunset }</span></p>
		</div>
		}
		<p className="error">{ props.error }</p>
	</div>
)

export default WeatherInfo;
